.edit-table-customization-wrap {

    .edit-form-group {

        .table-customization-tab-wrap {
            display: flex;

            .table-customization-tab-nav {
                display: flex;
                width: 100%;
                max-width: 125px;
                overflow: hidden;
                background: #E7EDF2;
                flex-direction: column;
                border-bottom-left-radius: 4px;

                button {
                    border: none;
                    outline: none;
                    cursor: pointer;
                    text-align: left;
                    padding: 14px 20px;
                    font-size: 16px;
                    color: #757678;
                    box-shadow: none;
                    font-weight: 600;
                    line-height: 21px;
                    margin-bottom: 2px;
                    background: transparent;
                    border-left: 4px solid transparent;
                    transition: all 0.3s ease;

                    &.active,
                    &:hover {
                        color: #2BB885;
                        background: #ffffff;
                        border-left-color: #2BB885;
                    }

                }

                button:first-child {
                    border-top-left-radius: 8px;
                }
            }

            .table-customization-tab-content {
                width: 100%;

                .table-customization-utility {
                    .edit-form-group {
                        h4 {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 16px;
                            color: #666873;
                            margin: 20px 0 15px 0;
                            display: flex;
                            align-items: center;

                            svg {
                                margin-left: 5px;
                            }
                        }

                        .exports-btns {
                            display: flex;
                            align-items: center;
                            justify-content: flex-start;
                            margin-left: -10px;
                            margin-right: -10px;
                            flex-wrap: wrap;
                            margin-bottom: -20px;

                            button {
                                margin-left: 10px;
                                margin-right: 10px;
                                display: inline-block;
                                padding: 10px 40px;
                                background: #ffffff;
                                border: 1px solid #e3e5e9;
                                border-radius: 6px;
                                font-style: normal;
                                margin-bottom: 20px;
                                font-weight: 600;
                                font-size: 14px;
                                line-height: 19px;
                                text-align: center;
                                color: #666873;
                                cursor: pointer;
                                outline: none;
                                box-shadow: none;
                                overflow: hidden;
                                transition: all 0.3s ease;

                                &.active {
                                    position: relative;
                                    border-color: #34a0fa;
                                    font-style: normal;
                                    font-weight: 600;
                                    font-size: 14px;
                                    line-height: 19px;
                                    text-align: center;
                                    color: #34a0fa;

                                    &::before {
                                        content: "";
                                        position: absolute;
                                        left: 7px;
                                        top: 3px;
                                        width: 3px;
                                        height: 7px;
                                        border-left: 2px solid white;
                                        border-bottom: 2px solid white;
                                        font-family: arial;
                                        transform: scaleX(-1) rotate(-45deg);
                                        z-index: 1;
                                    }

                                    &::after {
                                        content: "";
                                        position: absolute;
                                        left: -26px;
                                        top: -30px;
                                        width: 50px;
                                        height: 50px;
                                        background: #34a0fa;
                                        transform: rotate(47deg);
                                        z-index: 0;
                                    }

                                    &:hover {
                                        box-shadow: rgb(52, 160, 250, 0.3) 0px 0px 0px 5px;
                                    }
                                }

                                &:hover {
                                    box-shadow: rgb(240, 242, 248) 0px 0px 0px 5px;
                                }

                                &:focus {
                                    outline: none;
                                    border: 1px solid #e3e5e9;
                                    // box-shadow: rgb(240, 242, 248) 0px 0px 0px 5px;
                                    box-shadow: none;
                                }
                            }
                        }

                        .utility-checkbox-wrapper {
                            display: flex;
                            align-items: center;
                            justify-content: flex-start;
                            margin-left: -10px;
                            margin-right: -10px;
                            flex-wrap: wrap;
                            margin-bottom: -20px;

                            input[type="radio"] {
                                display: none;
                            }

                            .utility-checkboxees {
                                width: fit-content;
                                display: inline-block;
                                background: #ffffff;
                                border: 1px solid #ecf2f6;
                                border-radius: 6px;
                                white-space: nowrap;
                                padding: 10px 20px;
                                text-align: right;
                                display: inline-flex;
                                align-items: center;
                                justify-content: flex-start;
                                flex-flow: row-reverse;
                                margin-left: 10px;
                                margin-right: 10px;
                                margin-bottom: 20px;
                                transition: all 0.3s ease;
                                position: relative;

                                .control__indicator {
                                    width: 16px;
                                    height: 16px;
                                    border: 2px solid #ccd5dc;
                                    border-radius: 11px;
                                    margin-right: 7px;
                                    position: relative;
                                    box-sizing: border-box;

                                    &::after {
                                        content: "";
                                        position: absolute;
                                        left: 50%;
                                        top: 50%;
                                        width: 16px;
                                        height: 16px;
                                        transform: translate(-50%, -50%);
                                        border: 4px solid #34a0fa;
                                        border-radius: 8px;
                                        box-sizing: border-box;
                                        opacity: 0;
                                        visibility: hidden;
                                        z-index: -99999;
                                    }

                                    &.active {
                                        &::after {
                                            visibility: visible;
                                            opacity: 1;
                                            z-index: 1;
                                        }
                                    }
                                }

                                span {
                                    font-weight: 400;
                                    font-size: 14px;
                                    line-height: 19px;
                                    color: #1e1e1e;
                                    display: flex;
                                    align-items: center;
                                    justify-content: flex-start;

                                    svg {
                                        margin-left: 7px;
                                    }
                                }

                                input[type="checkbox"] {
                                    position: absolute;
                                    z-index: -1;
                                    opacity: 0;
                                    left: -9999px;

                                    &:checked~.control__indicator {
                                        &::after {
                                            opacity: 1;
                                            visibility: visible;
                                            z-index: 10;
                                        }
                                    }

                                    &:checked~span {
                                        font-style: normal;
                                        font-weight: 600;
                                        font-size: 14px;
                                        line-height: 19px;
                                        color: #34a0fa;
                                    }
                                }

                                &:hover {
                                    box-shadow: rgb(240, 242, 248) 0px 0px 0px 5px;
                                }

                                &.active {
                                    border-color: #34a0fa;
                                }
                            }
                        }

                        .cache-table {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 16px;
                            line-height: 16px;
                            color: #1e1e1e;
                            display: flex;

                            input[type="checkbox"] {
                                position: relative;
                                border: 1px solid #bbbdca;
                                background: none;
                                cursor: pointer;
                                line-height: 0;
                                margin: 0 0.6em 0 0;
                                outline: 0;
                                padding: 0 !important;
                                vertical-align: text-top;
                                height: 19px;
                                width: 19px;
                                -webkit-appearance: none;
                                border-radius: 5px;
                                outline: none;
                                box-shadow: none;
                                box-sizing: border-box;

                                &:checked {
                                    background-color: #34a0fa;
                                }

                                &:checked::before {
                                    content: "";
                                    position: absolute;
                                    right: 50%;
                                    top: 50%;
                                    width: 4px;
                                    height: 10px;
                                    border: solid #fff;
                                    border-width: 0 2px 2px 0;
                                    margin: -1px -1px 0 -1px;
                                    transform: rotate(45deg) translate(-50%, -50%);
                                    z-index: 2;
                                }

                                &:focus {
                                    outline: none;
                                    box-shadow: none;
                                    // border: 2px solid #34a0fa;
                                    border: 1px solid #bbbdca;

                                }
                            }

                            .span {
                                display: inline-flex;
                                align-items: center;
                                justify-content: flex-start;

                                svg {
                                    margin-left: 7px;
                                    vertical-align: middle;
                                }
                            }


                        }

                        /* &:last-child {
                            margin-top: 20px;
                            padding-bottom: 40px;
                        } */

                    }

                    .edit-form-group.cache-feature.frequent-mode {
                        padding: 20px 20px 18px;
                        background-color: #f6f7fe;
                        border-radius: 6px;
                        width: 100%;
                        margin-top: 5px;
                        display: flex;
                        flex-direction: column;
                        align-items: baseline;

                        .freq-settings {
                            display: flex;
                        }

                        .content-paragraph {
                            margin-top: 20px;
                            font-size: 14px;
                            color: #666 !important;
                            margin-bottom: 10px;
                            line-height: 1.4;
                        }
                    }



                    .edit-form-group.cache-feature {
                        gap: 3px;
                        display: flex;
                        align-items: center;

                        .cache-table {
                            margin-bottom: 0;
                        }

                        span.tooltip-cache {
                            display: flex;
                            line-height: 1;
                            align-items: center;

                            button.btn-pro.btn-new {
                                margin-top: 0;
                                margin-bottom: 0;
                            }
                        }

                        button.beta-badge {
                            outline: 0;
                            gap: 3px;
                            color: #F00;
                            background-color: rgba(255, 0, 0, 0.05);
                            box-shadow: none;
                            font-size: 11px;
                            font-style: normal;
                            font-weight: 600;
                            line-height: normal;
                            display: inline-flex;
                            align-items: center;
                            border-radius: 3.533px;
                            padding: 2px 7px 2px 7px;
                            border: 0.5px solid rgba(255, 0, 0, 0.05);
                            box-shadow: none !important;
