                            margin-left: 5px;

                            &:hover {
                                color: #fff;
                                background-color: #F00;
                            }

                        }


                    }

                    .feature-disabled {
                        opacity: 0.4;
                        cursor: not-allowed;

                        #disable-frequent-cache {
                            cursor: not-allowed;
                        }
                    }

                    .edit-form-group.special-feature {
                        gap: 3px;
                        display: flex;
                        margin-top: 23px;
                        align-items: center;
                        text-align: center;

                        .cache-table {
                            margin-bottom: 0;
                        }

                        span.tooltip-cache {
                            display: flex;
                            line-height: 1;
                            align-items: center;


                            button.btn-pro.btn-new {
                                margin-top: 0;
                                margin-bottom: 0;
                            }
                        }

                        button.btn-pro.btn-new {
                            outline: 0;
                            gap: 3px;
                            color: #2bb885;
                            background-color: rgba(43, 184, 133, 0.05);
                            box-shadow: none;
                            font-size: 11px;
                            font-style: normal;
                            font-weight: 600;
                            line-height: normal;
                            display: inline-flex;
                            align-items: center;
                            border-radius: 3.533px;
                            padding: 2px 7px 2px 7px;
                            border: 0.5px solid rgba(43, 184, 133, 0.05);
                            box-shadow: none !important;
                            transition: background-color 0.2s, color 0.2s;
                            margin-left: 5px;

                            &:hover {
                                color: #fff;
                                background-color: #2bb885;
                            }
                        }


                    }


                    // Allow sorting 
                    .edit-form-group.cache-feature.allow-sorting {
                        display: flex;
                        align-items: center;
                        margin-top: -16px;
                        margin-left: 1px;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 16px;
                        color: #1e1e1e;
                        display: flex;

                        label.singlesort-label {
                            margin-top: 9px;
                        }

                        // Checkbox desgin start 
                        input[type=checkbox] {
                            position: relative;
                            border: 1px solid #bbbdca;
                            background: none;
                            cursor: pointer;
                            line-height: 0;
                            margin: 0 0.6em 0 0;
                            outline: 0;
                            padding: 0 !important;
                            vertical-align: text-top;
                            height: 19px;
                            width: 19px;
                            -webkit-appearance: none;
                            border-radius: 5px;
                            outline: none;
                            box-shadow: none;
                            box-sizing: border-box;
                        }

                        input[type=checkbox]:checked {
                            background-color: #34a0fa;
                        }

                        input[type=checkbox]:checked::before {
                            content: "";
                            position: absolute;
                            right: 50%;
                            top: 50%;
                            width: 4px;
                            height: 10px;
                            border: solid #fff;
                            border-width: 0 2px 2px 0;
                            margin: -1px -1px 0 -1px;
                            transform: rotate(45deg) translate(-50%, -50%);
                            z-index: 2;
                        }

                        // checbox design 

                        span.select-wrapper {
                            margin: 5px;
                            position: relative;
                            display: inline-flex;

                            .icon {
                                right: 10px;
                                line-height: 0;
                                position: absolute;
                                top: calc(50% - 3px);
                                pointer-events: none;
                            }
                        }

                        select#singleshort-mode {
                            margin: 0;
                            color: #1E1E1E;
                            border-radius: 6px;
                            font-size: 16px;
                            font-style: normal;
                            box-shadow: none;
                            font-weight: 400;
                            line-height: normal;
                            background: #F8FCFF;
                            padding: 8px 37px 9px 10px;
                            border: 1px solid #BBBDCA;
                        }


                    }

                    // end s
                }

                .table-customization-style {
                    margin-bottom: 60px;

                    .edit-form-group {
                        h4 {
                            font-style: normal;
                            font-weight: 400;
                            font-size: 16px;
