                            color: #666873;
                            margin: 20px 0 15px 0;
                            display: flex;
                            align-items: center;

                            svg {
                                margin-left: 5px;
                            }
                        }

                        .style-checkbox-items {
                            display: flex;
                            align-items: center;
                            justify-content: flex-start;
                            margin-left: -10px;
                            margin-right: -10px;
