                                transition: all 0.3s ease;
                                position: relative;
                                font-style: normal;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 19px;
                                color: #1e1e1e;

                                .control__indicator {
                                    width: 16px;
                                    height: 16px;
                                    border: 2px solid #ccd5dc;
                                    border-radius: 11px;
                                    margin-right: 7px;
                                    position: relative;
                                    box-sizing: border-box;

                                    &::after {
                                        content: "";
                                        position: absolute;
                                        left: 50%;
                                        top: 50%;
                                        width: 16px;
                                        height: 16px;
                                        transform: translate(-50%, -50%);
                                        border: 4px solid #34a0fa;
                                        border-radius: 8px;
                                        box-sizing: border-box;
                                        opacity: 0;
                                        visibility: hidden;
                                        z-index: -99999;
                                    }

                                    &.active {
                                        &:after {
                                            visibility: visible;
                                            opacity: 1;
                                            z-index: 1;
                                        }
                                    }
                                }

                                span {
                                    font-weight: 400;
                                    font-size: 14px;
                                    line-height: 19px;
                                    color: #1e1e1e;
                                    display: flex;
                                    align-items: center;
                                    justify-content: flex-start;

                                    svg {
                                        margin-left: 7px;
                                    }
                                }

                                input[type="checkbox"] {
                                    position: absolute;
                                    z-index: -1;
                                    opacity: 0;
                                    left: -9999px;

                                    &:checked .control__indicator {
                                        &::after {
