                    }

                    .table-customization-layout {
                        display: flex;
                        flex-wrap: wrap;
                        grid-row-gap: 20px;
                        margin-top: 30px;
                        margin-bottom: 30px;
                        grid-column-gap: 30px;

                        .edit-form-group {
