// Frquent mode modal 
.frequent-modal-wrap {
    width: 100%;
    max-width: 700px;
    padding: 30px;
    position: relative;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0 6px 24px rgba(0, 0, 0, 0.15);

    .cross_sign {
        position: absolute;
        top: 16px;
        right: 16px;
        cursor: pointer;
        color: #6b7280;
        transition: color 0.2s;

        &:hover {
            color: #374151;
        }
    }

    .active-frequent-modal {
        h2 {
            font-size: 22px;
            font-weight: 600;
            color: #1f2937;
            margin: 0 0 14px;
            text-align: left;
        }

        p {
            margin: 0 0 24px;
            color: #4b5563;
            font-size: 15px;
            line-height: 1.5;
            text-align: left;
        }

        .content-note {
            display: flex;
            align-items: center;
            padding: 16px;
            background-color: #fffbeb;
            border: 1px solid #fff5d4;
            border-radius: 10px;
            margin-bottom: 24px;

            .content-note-icon {
                flex-shrink: 0;
                margin-right: 16px;

                svg {
                    color: #d97706;
                    display: block;
                }
            }

            .content-note-text {
                flex: 1;
                color: #78716c;
                line-height: 1.5;
                font-size: 14px;
            }
        }

        .action-buttons {
            display: flex;
            justify-content: flex-end;
            gap: 12px;
            margin-top: 24px;

            .swptls-button {
                padding: 10px 18px;
                border-radius: 6px;
                font-weight: 500;
                font-size: 14px;
                cursor: pointer;
                transition: all 0.2s ease;

                &.cancel-button {
                    background-color: transparent;
                    border: 1px solid #d1d5db;
                    color: #4b5563;

                    &:hover {
                        background-color: #f3f4f6;
                    }
                }

                &.confirm-button {
                    background-color: #3b82f6;
                    border: none;
                    color: white;

                    &:hover {
                        background-color: #2563eb;
                    }
                }
            }
        }
    }
}

