.table-style {
    display: flex;
    align-items: center;
    flex-direction: row;
    // margin-top: 25px;

    $toggle-on-color: #34A0FA; // On state color
    $toggle-off-color: #ccc; // Off state color
    $toggle-slider-color: #fff; // Slider color

    $toggle-width: 40px; // Width of the toggle switch
    $toggle-height: 20px; // Height of the toggle switch
    $toggle-slider-size: 18px; // Size of the slider

    label {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        color: #1e1e1e;
        text-align: center;
        display: flex;
        // Other label styles

        .toggle-switch {
            display: inline-block;
            position: relative;
            width: $toggle-width;
            height: $toggle-height;
            margin-right: 5px;

            input[type="checkbox"] {
                display: none; // Hide the actual checkbox input

                &:checked+.slider {
                    background-color: $toggle-on-color; // On state color
                }

                // Move the slider to the right for the "on" state
                &:checked+.slider:before {
                    transform: translateX(($toggle-width - 2) - $toggle-slider-size);
                }
            }

            .slider {
                position: absolute;
                cursor: pointer;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: $toggle-off-color; // Off state color
                transition: 0.4s;
                border-radius: calc($toggle-height / 2);
                width: $toggle-width;

                &:before {
                    position: absolute;
                    content: "";
                    height: $toggle-slider-size;
                    width: $toggle-slider-size;
                    left: 1px; // Adjust the position of the slider
                    bottom: 1px; // Adjust the position of the slider
                    background-color: $toggle-slider-color; // Slider color
                    transition: 0.4s;
                    border-radius: 50%;
                }
            }

            input[type="checkbox"]:disabled+.slider {
                background-color: #ccc; // Disabled state color
                cursor: not-allowed;
            }
        }
    }
}

// New UI Style

.switch-toggle {
    position: relative;
    margin-bottom: 13px;


    input[type="checkbox"] {
        top: 0;
        left: 0;
        opacity: 0;
        height: 100%;
        cursor: pointer;
        position: absolute;
    }

    label {
        color: #1E1E1E;
        font-weight: 400;
        font-size: 16px;
        padding-left: 50px;
        line-height: 100%;
        position: relative;
        display: inline-block;
        box-sizing: border-box;
    }

    label:before {
        content: '';
        top: 0;
        left: 0;
        min-width: 40px;
        height: 20px;
        position: absolute;
        background: #DCDDE4;
        display: inline-block;
        border-radius: 500px;
        box-sizing: border-box;
        transition: 0.2s ease-in;
    }

    label:after {
        content: '';
        position: absolute;
        min-width: 16px;
        height: 16px;
        border-radius: 500px;
        left: 2px;
        top: 2px;
        z-index: 2;
        background: #fff;
        box-sizing: border-box;
        transition: 0.2s ease-in;
    }

    input[type="checkbox"]:checked+label:before {
        background: #34A0FA;
    }

    input[type="checkbox"]:checked+label:after {
        left: 22px;
    }
}

