// Backend pagination style 
#table-preview.gswpts_default-style {
	background-color: #ffffff;
	--pagination-colors: #828282;

	.dataTables_wrapper.no-footer .dataTables_scrollBody {
		border: 0px solid;
	}

	div.dataTables_wrapper div.dataTables_info {
		padding-top: 0;
	}

	// Pagination for default backend 
	.paginate_button,
	.paginate_button.disabled,
	.ellipsis {
		font-size: 14px;
		border: none;
		margin: 0;
		padding: 9px 16px;
		font-weight: bold;
		color: #828282;
		background: transparent;
		border: 1px solid #E0E0E0;
		text-decoration: none;
		border-color: rgba(179, 179, 179, 0.3607843137);


		&:hover {
			background-color: rgba(0, 0, 0, 0.03);
			border-color: #b3b3b35c;
			color: rgba(56, 13, 13, .787) !important; // Get the jquery color 
		}

		@media screen and (max-width: 770px) {
			padding: 6px 9px !important;
		}

		@media screen and (max-width: 420px) {
			padding: 3px 6px !important;
			font-size: 12px;
		}

	}


	.dataTables_wrapper .dataTables_paginate .paginate_button.disabled:hover,
	.dataTables_wrapper .dataTables_paginate .paginate_button.disabled:active {
		cursor: default;
		color: #9f9c9c;
		border: 1px solid #E0E0E0;
		background: transparent;
		box-shadow: none;
	}

	// Current or active pagination
	a.paginate_button.current {
		background-color: #828282;
		color: #ffffff !important;

	}

	a.paginate_button.previous.disabled {
		color: #9f9c9c !important;

	}

	a.paginate_button.next.disabled {
		color: #9f9c9c !important;

	}

	a#create_tables_previous {
		border-radius: 0.28571429rem 0px 0px 0.28571429rem;
	}

	a#create_tables_next {
		border-radius: 0px 0.28571429rem 0.28571429rem 0px !important;
	}


}

