// Header Style
.swptls-header-wrap {
    .header-section {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 35px 0 30px;

        .header-title {
            margin: 0;
            font-size: 20px;
            line-height: 26px;
            color: #1E1E1E;
            font-weight: 400;
            margin-right: 10px;
        }

        .dashboard-logo {
            display: inline-flex;
            align-items: center;
            justify-content: flex-start;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 21px;
            color: #1e1e1e;
            text-decoration: none;
            transition: all 0.3s ease;

            .icon {
                display: block;
                max-width: 18px;
                width: 100%;
                height: 18px;
                margin-right: 8px;
                transition: all 0.3s ease;

                svg {
                    width: 100%;
                    height: 100%;
                }
            }

            &:focus {
                outline: none;
                border: none;
                box-shadow: none;
            }

            &:hover {
                .icon {
                    animation: bounce 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
                }

                color: #2bb885;
            }

            @media (max-width: 510.98px) {
                align-self: flex-start;
            }
        }

        .new-unlock-block {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .unlock {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                margin-right: 37px;
                cursor: pointer;

                .icon {
                    margin-right: 8px;
                    max-width: 15px;
                    width: 100%;
                    height: 17px;
                    transition: all 0.3s ease;
                }

                p {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 21px;
                    color: #1e1e1e;
                    margin: 0;

                    a {
                        text-decoration: none;
                        color: inherit;
                    }
                }

                &:hover {
                    .icon {
                        animation: bounce 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
                    }
                }
            }

            .whats-new {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                cursor: pointer;
                padding-right: 20px;

                .icon {
                    margin-right: 8px;
                    max-width: 18px;
                    width: 100%;
                    height: 16px;
                    transition: all 0.3s ease;
                }

                p {
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 21px;
                    color: #1e1e1e;
                    margin: 0;
                }

                &:hover {
                    .icon {
                        animation: bounce 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
                    }
                }
            }

            @media (max-width: 510.98px) {
                display: flex;
                align-items: flex-start;
                justify-content: center;
                flex-direction: column;

                .unlock {
                    margin-right: 0;
                    margin-bottom: 10px;
                }
            }
        }
    }
}

